@media all and (max-width: 1200px) {
    #students .shaixuan_box{
        left: 3%!important;
    }
}
#students .row .tipheight{
    height:435px;
}
#students .row .tip_select{
    height:465px;
}
#students .row .tips{
    font-size:25px;
    text-align:left;
    font-weight:900;
    margin-top:-20px;
    margin-bottom:-20px;
    padding:0 20px;
}
#students .row .tippoint{
    margin-top:50px;
    padding:0 20px;
    line-height:40px;
}
#students .is-active{
    color: #029348 !important;
}
.el-tabs__active-bar{
    background: #029348 !important;
}
#students .is-top :hover{
    color: #029348 !important;
}
#students .is-left :hover{
    color: #029348 !important;
}
#students .el-card{
    padding: 40px 0 40px 0;
}
#students .shaixaun{
    padding: 82px 0 32px 0;
}
.el-tabs__content{
    overflow:inherit;
    padding-bottom: 20px;
}
.el-card{
    transition: 0s;
}
</style>



